import React from "react";
import "./style.css";
import { ParallelCoordinates } from "react-parcoords";
import { dataArr } from './data.js';
import Table from './components/Table.jsx';
import Hover from './components/Hover.jsx';

class ParCoordinateSystem extends React.Component {
    constructor() {
        super()
        this.state = {
            ActiveDataPoints: dataArr,
            currentLine: [],
            data: dataArr,
            props: {},
        };
    }

    componentDidMount() {
        const dimensions = {
            VisualizationSkills: {
                title: "Visualization",
                type: "number"
            },
            DrawingSkills: {
                title: "Drawing and artistic",
                type: "number"
            },
            ProgrammingSkills: {
                title: "Programming",
                type: "number"
            },
            GraphicsSkills: {
                title: "Computer graphics",
                type: "number"
            },
            HCISkills: {
                title: "HCI programming",
                type: "number"
            },
            EvalSkills: {
                title: "User evaluation",
                type: "number"
            },
        };

        this.setState({
            props: {
                color: '#16c5ec',
                width: 1000,
                height: 310,
                dimensions,
                data: this.state.data,
                highlights: [],
                onBrush: this.OnBrushCall,
                onBrushEnd: this.OnBrushEndCall,
                onLineHover: this.OnHoverCall,
                onLinesHover: this.OnlinesHoverCall
                // onBrush: console.log,
                // onBrushEnd: console.log,
                // onLineHover: console.log,
                // onLinesHover: console.log
            }
        });
    };

    setActiveDataPoints = arr => {
        this.setState({ ActiveDataPoints: arr });
    };

    getActiveDataPoints = () => {
        return this.state.ActiveDataPoints;
    };

    setCurrentLine = arr => {
        this.setState({ currentLine: arr });
    };

    getCurrentLine = () => {
        return this.state.currentLine;
    };

    OnBrushCall = incomingData => {
        // TODO: Fill in!
    };

    OnBrushEndCall = incomingData => {
        let NewActiveData = [];
        incomingData.data.forEach(element => {
            console.log("???: " + element);
            NewActiveData.push(element);
        });
        this.setActiveDataPoints(NewActiveData);
    };

    OnHoverCall = (incData) => {
        let hovered = [];
        if (typeof incData !== "undefined") {
            hovered.push(incData);
        }
        this.setCurrentLine(hovered);
        console.log("hovering line: " + hovered);

    }

    render() {
        return (
            <div>
                <div id="chartDiv">
                    <ParallelCoordinates {...this.state.props} />
                </div>
                <div id="Hovering">
                    <h1>
                        <Hover currentLine={this.getCurrentLine} />
                    </h1>
                </div>
                <div id="TableDiv">
                    <Table current={this.getActiveDataPoints} />
                </div>
            </div>
        )
    }
}


export default ParCoordinateSystem;
