import React, { Component } from 'react'
import './Table.css';
import { logDOM } from '@testing-library/react';

class Table extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: props.current,
        };
    }

    generateTable = () => {
        let CurrentData = this.props.current();

        return CurrentData.map((data, index) => {
            console.log("keys: " + Object.keys(data));
            let { Alias, VisualizationSkills, DrawingSkills, ProgrammingSkills, GraphicsSkills, HCISkills, EvalSkills, Hobbies } = data;
            return (
                <tr key={index}>
                    <td>{Alias}</td>
                    <td>{VisualizationSkills}</td>
                    <td>{DrawingSkills}</td>
                    <td>{ProgrammingSkills}</td>
                    <td>{GraphicsSkills}</td>
                    <td>{HCISkills}</td>
                    <td>{EvalSkills}</td>
                    <td>{Hobbies}</td>
                </tr>
            )
        })
    };

    render() {
        return (
            <div>
                <h1 id="dataTableHeader">Detailed overview of datapoints</h1>
                <table id="dataTable">
                    <tbody>
                        <tr>
                            <th>Alias</th>
                            <th>Visualization</th>
                            <th>Drawing</th>
                            <th>Programming</th>
                            <th>Computer Graphics</th>
                            <th>HCI Programming</th>
                            <th>Evaluation</th>
                            <th>Hobbies</th>
                        </tr>
                        {this.generateTable()}
                    </tbody>
                </table>
            </div>
        )
    }
}

export default Table 