import React, { Component } from 'react';
import './Hover.css';

class Hover extends Component {
    constructor(props) {
        super(props)
    };

    getAlias = () => {
        let test = this.props.currentLine();
        const name = [];
        test.map((data, index) => {
            name.push(data.Alias);
        });
        return name[0];
    };

    render() {
        return (
            <div id="HoverBox">
                <p><b>Now Hovering: </b><i id="AliasHolder">{this.getAlias()}</i></p>
            </div>
        );
    }
}

export default Hover;